import React, { Component } from 'react';
import styled from "styled-components";
import { Link } from 'react-router-dom';

const TopMenuContainer = styled.nav`
  display : ${props => props.display};
  justify-content: center;
  align-items: center; 
`
const TopMenuItem = styled.div`
  display : flex;
  height : 50px;
  flex-direction: column;
  align-items: center; 
  &:hover .topMenuItem { color: yellow; font-size: 20px }
  &:hover .subItem1{ display: flex; }
`
const TopMenuList = styled.li` 
  padding : 0 30px 10px 30px;
  text-align : center;
  color : ${props => (props.topMenuItemColor)};
  list-style: none;
`
const TopMenuListChildContainer = styled.div`
  display : none;
  width: 50px;
  height: 50px;
  flex-direction: column;
  align-items: center; 
  @media ( max-width: 480px ) {
    display : flex;
  }
`
const TopMenuListChild = styled.a`
  padding : 0 30px 10px 30px;
  text-align : center;
  color : #ffffff;
  &:hover { color: #454545; }
`
const TopMenuListItem = [{itemLink : "/profile", itemName : "프로필"},{itemLink : "/portfolio", itemName : "포트폴리오"}]
class TopMenu extends Component {
  constructor(props){
    super()
    this.state={
      ...props,
    }
  }
  render() {
    return (  
        <TopMenuContainer display={this.props.display}>
        {TopMenuListItem.map((item,index)=>{return (
          <TopMenuItem key={index}>
            <TopMenuList key={index} topMenuItemColor={this.props.topMenuItemColor} item="item1">
              <Link to={item.itemLink}className="topMenuItem Link" >{item.itemName}</Link>
            </TopMenuList>
          </TopMenuItem>  
        )})}        
        </TopMenuContainer>
    );
  }
}

export default TopMenu;
