import React, { Component } from 'react';
import styled, { createGlobalStyle } from "styled-components";
import Header from '../components/Header'
import TopMenu from '../components/TopMenu'
import MainList from '../menu/mainList'
import { Route } from 'react-router-dom';
import { Home, Profile, Portfolio } from 'pages';

const GlobalStyle = createGlobalStyle`  
  html {
    height: 100vh; 
  }
  body {
    width :100%;    
    height: 100vh;  
    margin : 0 auto;
    padding : 0px;
    background-color :#000;
    color : white;
  }
  a.header{
    color : white;
  }
  a.topMenuItem{
    color : white;
  }
  a.Link{
    text-decoration : none;
  }
`
const Container = styled.div`
  display : flex;
  flex-direction: column;
  margin: 0 auto;
`
const HomeContainer = styled.div`
    display : flex;
    justify-content :center;
`

class App extends Component {
  constructor(props) {
    super()
    this.state = {
      ...props,
      commandList: [{ cmdName: "CMD" }, { cmdName: "LIST" }],
      cmdValue: "",
      isTopMenuVisible: "flex",
      isCmdListContainerVisible: "flex",
      topMenuItemColor: "#ff00ff"
    }
  }
  _cmdInput = (inputText) => {
    inputText = inputText.toLocaleLowerCase();
    switch (inputText) {
      case "showmenu":
        this.setState({
          isTopMenuVisible: "flex"
        })
        break;
      case "hidemenu":
        this.setState({
          isTopMenuVisible: "none"
        })
        break;
      case "showmenulist":
        this.setState({
          isCmdListContainerVisible: "flex"
        })
        break;
      case "hidemenulist":
        this.setState({
          isCmdListContainerVisible: "none"
        })
        break;
      default:
        alert("명령어 없음")
    }

  }
  _topItemMouseOver() {
    if (this.state.topMenuItemColor === "#ffffff") {
      this.setState({ topMenuItemColor: "#121212" })
    } else {
      this.setState({ topMenuItemColor: "#ffffff" })
    }
  }
  render() {
    return (
      <Container>
        <GlobalStyle />
        <Header></Header>
        <TopMenu 
          display={this.state.isTopMenuVisible}
          topMenuItemColor={this.state.topMenuItemColor}>
        </TopMenu>
        {/* <MainList
          cmdInput={this._cmdInput}
          isCmdListContainerVisible={this.state.isCmdListContainerVisible}
        ></MainList> */}
        <HomeContainer>
          <Route exact path="/" component={Home}/>
          <Route path="/profile" component={Profile}/>
          <Route path="/portfolio" component={Portfolio}/>
        </HomeContainer>

      </Container>

    );
  }
}

export default App;
