import React, { Component } from 'react';
import styled, {keyframes} from "styled-components";
const windowWidth = window.innerWidth;
const windowHeight = window.innerHeight;




const MainWrapper = styled.div`
    margin-top : 10%;
    height : ${props => (props.height)};
    width : ${props => (props.width)};
`
const HomeTop = styled.div`
    display : flex;
    align-items: center;
    width:100%;
    height:20%;
`
const HomeCenter = styled.div`
    width:100%;
    height:80%;
    position: relative;
`
const HomeRight = styled.div`
    display : flex;
    align-items: center;
    justify-content:center;
    width:30%;
    height:50%;
    position: absolute;
    right:0;
    bottom:0;
`
const HomeMonitor = styled.div`
    display : flex;
    width:80%;
    height:100%;
    position: absolute;
    justify-content:center;
    align-items:center;
`
const HomeMonitorItem = styled.div`
display : flex;
    justify-content:center;
    align-items:center;
    width:70%;
    height:40%;
    position: absolute;
`
const MonitorItemAnimation = keyframes`
  from {
    margin-right: 100%;
  }

  to {
    margin-right: 0%;
  }
`

const  MonitorItem = styled.a`
  animation : ${MonitorItemAnimation} 1s;
`

const TopItemAnimation = keyframes`
  from {
    margin-top: -10%;
  }

  to {
    margin-top: 0%;
  }
`

const TopItem = styled.a`
  animation : ${TopItemAnimation} 1s;
`
const RightItemAnimation = keyframes`
  from {
    margin-left: 100%;
  }

  to {
    margin-left: 0%;
  }
`

const RightItem = styled.a`
  animation : ${RightItemAnimation} 1s;
`

class MainContainer extends Component {
  constructor(props){
    super()
    this.state={
      ...props,
      wrapperWidth : windowWidth/2 +"px",
      wrapperHeight : windowHeight/2 +"px"
    }
  }
  updateWindowDimensions=() =>{
    this.setState({ wrapperWidth: window.innerWidth/2 +"px", wrapperHeight: window.innerHeight/2 +"px" });
  }
  componentDidMount(){
    window.addEventListener('resize', this.updateWindowDimensions);
  }
  render() {
    return (  
        <MainWrapper width={this.state.wrapperWidth} height={this.state.wrapperHeight}>     
          {/* TOP */}
          <HomeTop>
            <TopItem>TOP 내용</TopItem>
          </HomeTop>
          <HomeCenter>     
          {/* COMPUTER */}
          <HomeMonitor>
            <HomeMonitorItem>
              <MonitorItem>모니터 이미지</MonitorItem>
            </HomeMonitorItem>
          </HomeMonitor>
          {/* RIGHT */}       
            <HomeRight>
              <RightItem>오른쪽 이미지</RightItem>
            </HomeRight>
          </HomeCenter>
        </MainWrapper>
    );
  }
}

export default MainContainer;
